/* GLOBAL */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg: #050816;
  --bg-elevated: #0b1020;
  --accent: #4f46e5;
  --accent-soft: rgba(79, 70, 229, 0.18);
  --text-main: #f9fafb;
  --text-muted: #9ca3af;
  --border-subtle: rgba(148, 163, 184, 0.25);
  --card-radius: 18px;
  --shadow-soft: 0 18px 45px rgba(15, 23, 42, 0.75);
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    sans-serif;
  background: radial-gradient(circle at top, #111827 0, #020617 55%);
  color: var(--text-main);
}

/* SHELL LAYOUT */

.app-root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: 32px 16px;
}

.shell {
  width: 100%;
  max-width: 1120px;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 18px;
}

/* SIDEBAR STRIP */

.sidebar {
  width: 72px;
  background: linear-gradient(180deg, #1f2937, #020617);
  border-radius: 999px;
  padding: 18px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px;
  box-shadow: 0 18px 40px rgba(15, 23, 42, 0.9);
  position: sticky;
  top: 32px;
  height: fit-content;
}

.logo-circle {
  width: 40px;
  height: 40px;
  border-radius: 999px;
  background: radial-gradient(circle at 30% 30%, #facc15, #f97316);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: #111827;
  font-size: 1.2rem;
}

.sidebar-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
  font-size: 0.7rem;
}

.sidebar-name {
  font-weight: 600;
  letter-spacing: 0.05em;
}

.sidebar-role {
  color: var(--text-muted);
  text-transform: uppercase;
}

/* PAGE AREA */

.page {
  background: radial-gradient(circle at top left, #1e293b 0, #020617 55%);
  border-radius: 28px;
  padding: 26px 26px 22px;
  border: 1px solid rgba(148, 163, 184, 0.4);
  box-shadow: var(--shadow-soft);
  display: flex;
  flex-direction: column;
  gap: 18px;
}

/* HERO */

.hero {
  border-radius: 22px;
  padding: 26px 24px 26px;
  background: radial-gradient(circle at top left, #4f46e5 0, #020617 55%);
  position: relative;
  overflow: hidden;
}

.hero::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at top right,
    rgba(56, 189, 248, 0.32),
    transparent 58%
  );
  opacity: 0.7;
  pointer-events: none;
}

.hero-main {
  position: relative;
  z-index: 1;
  max-width: 760px;
}

/* New: hero layout with avatar */

.hero-top-row {
  display: flex;
  align-items: center;
  gap: 18px;
  justify-content: space-between;
}

.hero-text-block {
  flex: 1 1 auto;
}

.hero-tagline {
  color: #e5e7eb;
  font-size: 0.95rem;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  margin-bottom: 8px;
}

.hero-title {
  font-size: 2.2rem;
  line-height: 1.15;
  margin-bottom: 10px;
}

.hero-subtitle {
  font-size: 0.95rem;
  color: #e5e7eb;
  max-width: 36rem;
}

.hero-subtitle strong {
  color: #f9fafb;
}

.hero-avatar-wrapper {
  flex: 0 0 auto;
}

.hero-avatar {
  width: 96px;
  height: 96px;
  border-radius: 999px;
  border: 2px solid rgba(248, 250, 252, 0.7);
  object-fit: cover;
  box-shadow: 0 12px 30px rgba(15, 23, 42, 0.85);
}

/* HERO BADGES & BUTTONS */

.hero-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 14px 0 18px;
}

.pill {
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 0.78rem;
  border: 1px solid transparent;
}

.pill-primary {
  background-color: #f97316;
  color: #111827;
  font-weight: 600;
}

.pill-soft {
  background-color: rgba(15, 23, 42, 0.75);
  border-color: rgba(148, 163, 184, 0.5);
  color: #e5e7eb;
}

.hero-actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 8px;
}

.btn {
  padding: 8px 16px;
  border-radius: 999px;
  font-size: 0.9rem;
  border: 1px solid transparent;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-primary {
  background-color: #f97316;
  color: #111827;
  font-weight: 600;
  box-shadow: 0 10px 25px rgba(251, 146, 60, 0.55);
}

.btn-primary:hover {
  filter: brightness(1.05);
}

.btn-ghost {
  border-color: rgba(226, 232, 240, 0.6);
  color: #e5e7eb;
  background: rgba(15, 23, 42, 0.6);
}

.btn-ghost:hover {
  background: rgba(15, 23, 42, 0.9);
}

/* new: outline button for resume */
.btn-outline {
  border-color: rgba(249, 115, 22, 0.8);
  background: transparent;
  color: #fed7aa;
}

.btn-outline:hover {
  background: rgba(249, 115, 22, 0.12);
}

/* MAIN GRID */

.main-grid {
  display: grid;
  grid-template-columns: minmax(0, 1.2fr) minmax(0, 1fr);
  gap: 16px;
}

/* CARD */

.card {
  background: var(--bg-elevated);
  border-radius: var(--card-radius);
  padding: 18px 18px 20px;
  border: 1px solid var(--border-subtle);
  box-shadow: 0 18px 35px rgba(15, 23, 42, 0.65);
}

.full-width {
  grid-column: 1 / -1;
}

.section-title {
  font-size: 1.1rem;
  margin-bottom: 8px;
}

.section-intro {
  font-size: 0.9rem;
  color: var(--text-muted);
  margin-bottom: 14px;
}

/* SKILLS GRID */

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px 20px;
  margin-top: 8px;
}

.skill-heading {
  font-size: 0.85rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: #e5e7eb;
  margin-bottom: 4px;
}

.skills-grid ul {
  list-style: none;
  font-size: 0.86rem;
  color: var(--text-muted);
}

.skills-grid li {
  margin-bottom: 3px;
}

/* TIMELINE */

.timeline {
  list-style: none;
  margin-top: 6px;
  padding-left: 0;
  border-left: 1px solid rgba(55, 65, 81, 0.7);
}

.timeline li {
  position: relative;
  padding-left: 16px;
  margin-bottom: 12px;
}

.timeline-dot {
  width: 9px;
  height: 9px;
  border-radius: 999px;
  background-color: #f97316;
  position: absolute;
  left: -5px;
  top: 4px;
}

.timeline-content h3 {
  font-size: 0.95rem;
  margin-bottom: 2px;
}

.timeline-content p {
  font-size: 0.85rem;
  color: var(--text-muted);
}

/* PROJECTS */

.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 14px;
  margin-top: 10px;
}

.project-card {
  border-radius: 14px;
  padding: 12px 12px 14px;
  background: radial-gradient(circle at top, #111827, #020617);
  border: 1px solid rgba(148, 163, 184, 0.35);
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.8);
  transition: transform 0.16s ease, box-shadow 0.16s ease,
    border-color 0.16s ease;
}

.project-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 40px rgba(15, 23, 42, 0.95);
  border-color: #f97316;
}

.project-card h3 {
  font-size: 0.98rem;
  margin-bottom: 4px;
}

.project-card p {
  font-size: 0.86rem;
  color: var(--text-muted);
}

.project-tech {
  margin-top: 6px;
  font-size: 0.8rem;
  color: #e5e7eb;
}

/* CONTACT */

.contact-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 10px;
}

.contact-label {
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: var(--text-muted);
  margin-bottom: 4px;
}

/* LINKS */

a {
  color: #f97316;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* FOOTER */

.footer {
  padding-top: 6px;
  border-top: 1px solid rgba(148, 163, 184, 0.4);
  font-size: 0.8rem;
  color: var(--text-muted);
  display: flex;
  justify-content: flex-end;
}

/* RESPONSIVE */

@media (max-width: 900px) {
  .shell {
    grid-template-columns: 1fr;
  }

  .sidebar {
    flex-direction: row;
    width: 100%;
    border-radius: 18px;
    justify-content: flex-start;
  }

  .page {
    padding: 18px 16px 16px;
  }

  .main-grid {
    grid-template-columns: 1fr;
  }

  .projects-grid {
    grid-template-columns: 1fr;
  }

  .hero-top-row {
    flex-direction: row;
    align-items: flex-start;
  }
}

@media (max-width: 640px) {
  .app-root {
    padding: 16px 10px;
  }

  .hero-title {
    font-size: 1.7rem;
  }

  .hero {
    padding: 20px 18px 18px;
  }

  .skills-grid {
    grid-template-columns: 1fr;
  }

  .hero-top-row {
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .hero-avatar {
    width: 80px;
    height: 80px;
  }
}
